import React from "react";
import styled from "styled-components";


function Modal({clearCurrentGamesDispatch, currentScreenShotsSelector, currentGameSelector, currentLogosSelector}) {

    const htmlString = currentGameSelector ? currentGameSelector.description:'null'

  return (
    <Shadow
      onClick={(e) => {
        e.target.classList.contains("target") &&
          clearCurrentGamesDispatch({ type: "CLEAR_CURRENT_GAME" });
      }}
      className="target"
    >
      <Container>
        <DM1>
          {currentScreenShotsSelector &&
            currentScreenShotsSelector.results.map((item) => (
              <ModalImg key={item.id} src={item.image} alt="name" />
            ))}
        </DM1>
        <DM2>
          <h1>{currentGameSelector.name}</h1>
          <div>
            {currentLogosSelector.map((item, index) => (
              <ModalLogo key={index} src={item.props.src} />
            ))}
          </div>
          <DataGrid>
            <div>
              <h3>rating:</h3>
              <p>{currentGameSelector.rating}</p>
            </div>
            <div>
              <h3>genres:</h3>
              <p>
                {currentGameSelector.genres.map((item) => (
                  <span key={item.id}>{item.name} | </span>
                ))}
              </p>
            </div>
            <div>
              <h3>developers:</h3>
              <p>
                {currentGameSelector.developers.map((item) => (
                  <span key={item.id}>{item.name} | </span>
                ))}
              </p>
            </div>
            <div>
              <h3>released:</h3>
              <p>{currentGameSelector.released}</p>
            </div>
          </DataGrid>
          <div>
            {currentGameSelector.clip && (
              <ModalVid src={currentGameSelector.clip}></ModalVid>
            )}
            {currentGameSelector.background_image && (
              <ModalMainImg src={currentGameSelector.background_image} />
            )}
          </div>
          <Description>
            <h3>description:</h3>
            <div dangerouslySetInnerHTML={{ __html: htmlString }} />
          </Description>
        </DM2>
      </Container>
    </Shadow>
  );
}

export default Modal;



const Description = styled.div`
  margin: 0.5rem 0;
  text-align: justify;
  word-wrap: break-word;
`

const DataGrid = styled.div`
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  margin: 0.5rem 0;
`

const ModalVid = styled.video`
  width:100%;
  display:block;
  border-radius: 5px;
`

const ModalMainImg = styled.img`
  width:100%;
  display:block;
  border-radius: 5px;
`

const ModalLogo = styled.img`
  width:2rem;
  margin:0.25rem;
  border-radius: 5px;
`

const ModalImg = styled.img`
  width:100%;
  display:block;
`

const DM1 = styled.div`
  width:50%;
  overflow-y:auto;
  &::-webkit-scrollbar {
    width: 5px;
  }
  @media (max-width:786px) {
    width:100%;
  }
`
const DM2 = styled.div`
  width:50%;
  padding:0.5rem;
  overflow-y: auto;
  &::-webkit-scrollbar {
  width: 5px;
  }
  @media (max-width: 786px) {
    width:100%;
  }
`

const Container = styled.div`
  position: absolute;
  top:75px;
  left:5%;
  width:90%;
  height:85vh;
  background-color: white;
  color:black;
  display:flex;
  z-index:100;
  @media (max-width: 786px) {
    flex-direction: column-reverse;
  }
`

const Shadow = styled.div`
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-color: rgba(0,0,0,0.5);
  display:flex;
  justify-content: center;
  align-items: center;
  z-index:100;
`
