import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import Modal from "../components/Modal";

const Card = React.lazy(() => import("../components/Card"));

function MainPage() {
  const [orderListAppear, setOrderListAppear] = useState(false);
  const [platformListAppear, setPlatformListAppear] = useState(false);
  const [isPopular, setIsPopular] = useState(true);
  const [isUpcoming, setIsUpcoming] = useState(false);
  const [isNew, setIsNew] = useState(false);

  const [isSpecific, setIsSpecific] = useState(false);

  const [isPcSpecific, setIsPcSpecific] = useState(false);
  const [isPlaystationSpecific, setIsPlaystationSpecific] = useState(false);
  const [isXboxSpecific, setIsXboxSpecific] = useState(false);
  const [isMacSpecific, setIsMacSpecific] = useState(false);
  const [isAndroidSpecific, setIsAndroidSpecific] = useState(false);
  const [isLinuxSpecific, setIsLinuxSpecific] = useState(false);
  const [isNintendoSpecific, setIsNintendoSpecific] = useState(false);

  const [isSingleShow, setIsSingleShow] = useState(false);

  const popSelector = useSelector((state) => state.gamesReducer.popularGames);
  const upSelector = useSelector((state) => state.gamesReducer.upcomingGames);
  const newSelector = useSelector((state) => state.gamesReducer.newGames);

  const pcPopSelector = useSelector((state) => state.gamesReducer.pcPop);
  const pcNewSelector = useSelector((state) => state.gamesReducer.pcNew);
  const pcUpSelector = useSelector((state) => state.gamesReducer.pcUp);

  const playPopSelector = useSelector((state) => state.gamesReducer.playPop);
  const playNewSelector = useSelector((state) => state.gamesReducer.playNew);
  const playUpSelector = useSelector((state) => state.gamesReducer.playUp);

  const xboxPopSelector = useSelector((state) => state.gamesReducer.xboxPop);
  const xboxNewSelector = useSelector((state) => state.gamesReducer.xboxNew);
  const xboxUpSelector = useSelector((state) => state.gamesReducer.xboxUp);

  const ninPopSelector = useSelector((state) => state.gamesReducer.ninPop);
  const ninNewSelector = useSelector((state) => state.gamesReducer.ninNew);
  const ninUpSelector = useSelector((state) => state.gamesReducer.ninUp);

  const macPopSelector = useSelector((state) => state.gamesReducer.macPop);
  const macNewSelector = useSelector((state) => state.gamesReducer.macNew);
  const macUpSelector = useSelector((state) => state.gamesReducer.macUp);

  const linPopSelector = useSelector((state) => state.gamesReducer.linPop);
  const linNewSelector = useSelector((state) => state.gamesReducer.linNew);
  const linUpSelector = useSelector((state) => state.gamesReducer.linUp);

  const andPopSelector = useSelector((state) => state.gamesReducer.andPop);
  const andNewSelector = useSelector((state) => state.gamesReducer.andNew);
  const andUpSelector = useSelector((state) => state.gamesReducer.andUp);

  const searchedSelector = useSelector(
    (state) => state.gamesReducer.searchedGames
  );

  const currentGameSelector = useSelector(
    (state) => state.gamesReducer.current
  );
  const currentScreenShotsSelector = useSelector(
    (state) => state.gamesReducer.currentScreenShots
  );
  const currentLogosSelector = useSelector(
    (state) => state.gamesReducer.currentLogos
  );

  const clearSearchGamesDispatch = useDispatch();
  const clearCurrentGamesDispatch = useDispatch();

  const useHandleClickOutside = (ref, element) => {
    useEffect(() => {
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };

      function handleClickOutside(event) {
        if (ref.current && !ref.current.contains(event.target)) {
          switch (element) {
            case 1:
              setOrderListAppear(false);
              break;
            case 2:
              setPlatformListAppear(false);
              break;
            default:
              break;
          }
        }
      }
    }, [ref, element]);
  };

  const wrapperRef1 = useRef(null);
  const wrapperRef2 = useRef(null);
  useHandleClickOutside(wrapperRef1, 1);
  useHandleClickOutside(wrapperRef2, 2);

  const handleGameShow = (Pop, New, Upcoming) => {
    setIsPopular(Pop);
    setIsUpcoming(New);
    setIsNew(Upcoming);
    clearSearchGamesDispatch({ type: "CLEAR_SEARCHED_GAMES", payload: "" });
    setOrderListAppear(false);
  };

  const handleFilteredGameShow = (
    pc,
    playstation,
    xbox,
    nintendo,
    mac,
    linux,
    android
  ) => {
    setIsSpecific(true);
    setIsPcSpecific(pc);
    setIsPlaystationSpecific(playstation);
    setIsXboxSpecific(xbox);
    setIsNintendoSpecific(nintendo);
    setIsMacSpecific(mac);
    setIsLinuxSpecific(linux);
    setIsAndroidSpecific(android);
    clearSearchGamesDispatch({ type: "CLEAR_SEARCHED_GAMES", payload: "" });
    setPlatformListAppear(false);
  };

  let orderLabel;
  if (isPopular) {
    orderLabel = "popular";
  } else if (isNew) {
    orderLabel = "new";
  } else if (isUpcoming) {
    orderLabel = "upcoming";
  } else {
    orderLabel = "";
  }

  let platformLabel = "";
  if (!isSpecific) {
    platformLabel = "all";
  } else if (isPcSpecific) {
    platformLabel = "pc";
  } else if (isPlaystationSpecific) {
    platformLabel = "playStation";
  } else if (isXboxSpecific) {
    platformLabel = "Xbox";
  } else if (isNintendoSpecific) {
    platformLabel = "Nintendo";
  } else if (isMacSpecific) {
    platformLabel = "Mac";
  } else if (isLinuxSpecific) {
    platformLabel = "Linux";
  } else if (isAndroidSpecific) {
    platformLabel = "Android";
  } else {
    platformLabel = "";
  }

  if (searchedSelector) {
    orderLabel = "search";
    platformLabel = "";
  }

  return (
    <D>
      <P>Top Picks</P>
      <P1>based on your ratings</P1>
      <D1>
        <D1_1
          onClick={() => setOrderListAppear(!orderListAppear)}
          ref={wrapperRef1}
        >
          <p>
            order by : <span>{orderLabel}</span>
            <i className="bi bi-caret-down"></i>
          </p>
          {orderListAppear && (
            <D1S1 onClick={(e) => e.stopPropagation()}>
              <ul>
                <li onClick={() => handleGameShow(true, false, false, false)}>
                  popular games
                </li>
                <li onClick={() => handleGameShow(false, false, true, false)}>
                  new games
                </li>
                <li onClick={() => handleGameShow(false, true, false, false)}>
                  upcoming games
                </li>
              </ul>
            </D1S1>
          )}
        </D1_1>

        <D1_1
          onClick={() => setPlatformListAppear(!platformListAppear)}
          ref={wrapperRef2}
        >
          <p>
            platforms : <span>{platformLabel}</span>{" "}
            <i className="bi bi-caret-down"></i>
          </p>
          {platformListAppear && (
            <D1S2 onClick={(e) => e.stopPropagation()}>
              <ul>
                <li
                  onClick={() => {
                    setIsSpecific(false);
                    setPlatformListAppear(false);
                  }}
                >
                  None
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      true,
                      false,
                      false,
                      false,
                      false,
                      false,
                      false
                    )
                  }
                >
                  PC
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      false,
                      true,
                      false,
                      false,
                      false,
                      false,
                      false
                    )
                  }
                >
                  PlayStation
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      false,
                      false,
                      true,
                      false,
                      false,
                      false,
                      false
                    )
                  }
                >
                  Xbox
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      false,
                      false,
                      false,
                      true,
                      false,
                      false,
                      false
                    )
                  }
                >
                  Nintendo
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      false,
                      false,
                      false,
                      false,
                      true,
                      false,
                      false
                    )
                  }
                >
                  Mac
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      false,
                      false,
                      false,
                      false,
                      false,
                      true,
                      false
                    )
                  }
                >
                  Linux
                </li>
                <li
                  onClick={() =>
                    handleFilteredGameShow(
                      false,
                      false,
                      false,
                      false,
                      false,
                      false,
                      true
                    )
                  }
                >
                  Android
                </li>
              </ul>
            </D1S2>
          )}
        </D1_1>

        <D1_2>
          <i
            className="bi bi-border-all"
            onClick={() => setIsSingleShow(false)}
          ></i>
          <i
            className="bi bi-bookshelf"
            onClick={() => setIsSingleShow(true)}
          ></i>
        </D1_2>
      </D1>
      <GamesGridContainer isSingleShow={isSingleShow}>
        {searchedSelector &&
          searchedSelector.results.map((game) => (
            <Card key={game.id} game={game} />
          ))}
        {!searchedSelector &&
          isPopular &&
          !isSpecific &&
          popSelector &&
          popSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isUpcoming &&
          !isSpecific &&
          upSelector &&
          upSelector.results.map((game) => (
            <React.Suspense fallback={<div>Loading...</div>}>
              <Card key={game.id} game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          !isSpecific &&
          newSelector &&
          newSelector.results.map((game) => (
            <React.Suspense fallback={<div>Loading...</div>}>
              <Card key={game.id} game={game} />
            </React.Suspense>
          ))}

       
        {!searchedSelector &&
          isNew &&
          isPcSpecific &&
          newSelector &&
          pcNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          isPlaystationSpecific &&
          newSelector &&
          playNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          isXboxSpecific &&
          newSelector &&
          xboxNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          isMacSpecific &&
          newSelector &&
          macNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          isAndroidSpecific &&
          newSelector &&
          andNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          isLinuxSpecific &&
          newSelector &&
          linNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isNew &&
          isNintendoSpecific &&
          newSelector &&
          ninNewSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}

     
        {!searchedSelector &&
          isPopular &&
          isPcSpecific &&
          newSelector &&
          pcPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isPopular &&
          isPlaystationSpecific &&
          newSelector &&
          playPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isPopular &&
          isXboxSpecific &&
          newSelector &&
          xboxPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isPopular &&
          isMacSpecific &&
          newSelector &&
          macPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isPopular &&
          isAndroidSpecific &&
          newSelector &&
          andPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isPopular &&
          isLinuxSpecific &&
          newSelector &&
          linPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isPopular &&
          isNintendoSpecific &&
          newSelector &&
          ninPopSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}

        
        {!searchedSelector &&
          isUpcoming &&
          isPcSpecific &&
          newSelector &&
          pcUpSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isUpcoming &&
          isPlaystationSpecific &&
          newSelector &&
          playUpSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isUpcoming &&
          isXboxSpecific &&
          newSelector &&
          xboxUpSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isUpcoming &&
          isMacSpecific &&
          newSelector &&
          macUpSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isUpcoming &&
          isAndroidSpecific &&
          newSelector &&
          andUpSelector.results.map((game) => (
            <Card key={game.id} game={game} />
          ))}
        {!searchedSelector &&
          isUpcoming &&
          isLinuxSpecific &&
          newSelector &&
          linUpSelector.results.map((game) => (
            <React.Suspense key={game.id} fallback={<div>Loading...</div>}>
              <Card game={game} />
            </React.Suspense>
          ))}
        {!searchedSelector &&
          isUpcoming &&
          isNintendoSpecific &&
          newSelector &&
          ninUpSelector.results.map((game) => (
            <Card key={game.id} game={game} />
          ))}
      </GamesGridContainer>
      <PE>Nothing else here ...</PE>
      {currentGameSelector && (
        <Modal
          clearCurrentGamesDispatch={clearCurrentGamesDispatch}
          currentScreenShotsSelector={currentScreenShotsSelector}
          currentGameSelector={currentGameSelector}
          currentLogosSelector={currentLogosSelector}
        />
      )}
    </D>
  );
}

const PE = styled.p`
  font-size: 3rem;
  margin: 45rem 0 2rem 0;
`;

const GamesGridContainer = styled.div`
  display: grid;
  grid-template-columns: ${(props) =>
    props.isSingleShow
      ? "repeat(auto-fit, 50%)"
      : "repeat(auto-fit, minmax(250px,1fr))"};
  justify-content: center;
  gap: 2rem 2rem;
  margin-top: 2rem;
`;

const D = styled.div``;

const P = styled.p`
  font-size: 4rem;
  font-weight: bold;
`;
const P1 = styled.p`
  margin-bottom: 3rem;
`;

const D1 = styled.div`
  display: flex;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
  }
`;
const D1_1 = styled.div`
  display: flex;
  align-items: center;
  background-color: #202020;
  padding: 0.5rem;
  border-radius: 5px;
  margin-right: 0.25rem;
  position: relative;
  width: 14rem;
  cursor: pointer;
  p,
  span,
  i {
    background-color: transparent;
  }
  i {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
  }

  @media (max-width: 768px) {
    margin-bottom: 0.5rem;
  }
`;
const D1S1 = styled.div`
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.5rem;
  background-color: white;
  border-radius: 5px;
  ul {
    list-style: none;
    background-color: white;
  }
  li {
    font-size: 0.8rem;
    background-color: white;
    color: black;
    border-radius: 5px;
    padding: 0.5rem;
    :hover {
      background-color: lightgray;
    }
  }
`;

const D1S2 = styled.div`
  z-index: 100;
  position: absolute;
  top: 0rem;
  left: 0;
  padding: 0.5rem;
  background-color: white;
  border-radius: 5px;
  ul {
    list-style: none;
    background-color: white;
  }
  li {
    font-size: 0.8rem;
    background-color: white;
    color: black;
    border-radius: 5px;
    padding: 0.5rem;
    :hover {
      background-color: lightgray;
    }
  }
`;

const D1_2 = styled.div`
  display: flex;
  align-items: center;
  margin-left: auto;
  p {
    margin-right: 0.5rem;
  }
  i {
    display: inline-flex;
    background-color: #202020;
    padding: 0.5rem;
    margin: 0 0.25rem;
    border-radius: 5px;
    font-size: 2rem;
    cursor: pointer;
  }
  @media (max-width: 768px) {
    margin-left: 0rem;
  }
`;

export default MainPage;