import {
     GET_POPULAR_GAMES, GET_NEW_GAMES, GET_UPCOMING_GAMES, GET_SEARCHED_GAMES, SET_LOADING, GAMES_ERROR,
     GET_PC_POP, GET_PLAY_POP, GET_XBOX_POP, GET_NIN_POP, GET_MAC_POP, GET_LIN_POP, GET_AND_POP,
     GET_PC_NEW, GET_PLAY_NEW, GET_XBOX_NEW, GET_NIN_NEW, GET_MAC_NEW, GET_LIN_NEW, GET_AND_NEW,
     GET_PC_UP, GET_PLAY_UP, GET_XBOX_UP, GET_NIN_UP, GET_MAC_UP, GET_LIN_UP, GET_AND_UP,
        } from '../types';

import {
     popular_games, new_games, upcoming_games, searched_games, game, screenshots,
     pc_pop, play_pop, xbox_pop, nin_pop, mac_pop, lin_pop, and_pop,
     pc_new, play_new, xbox_new, nin_new, mac_new, lin_new, and_new,
     pc_up, play_up, xbox_up, nin_up, mac_up, lin_up, and_up,
        } from '../api'
 
export const getPopularGames = () => async dispatch => {
    try {
        setLoading();
        const popularGamesData = await fetch(popular_games())
        const popularGames = await popularGamesData.json()

        dispatch({
            type: GET_POPULAR_GAMES,
            payload: popularGames 
        });
    }
    catch (err) {
        dispatch({
            type: GAMES_ERROR,
            payload: err.response.data
        })
    }
}

