import {
  GET_POPULAR_GAMES,
  GET_NEW_GAMES,
  GET_UPCOMING_GAMES,
  GET_SEARCHED_GAMES,
  SET_LOADING,
  GAMES_ERROR,
  GET_PC_POP,
  GET_PLAY_POP,
  GET_XBOX_POP,
  GET_NIN_POP,
  GET_MAC_POP,
  GET_LIN_POP,
  GET_AND_POP,
  GET_PC_NEW,
  GET_PLAY_NEW,
  GET_XBOX_NEW,
  GET_NIN_NEW,
  GET_MAC_NEW,
  GET_LIN_NEW,
  GET_AND_NEW,
  GET_PC_UP,
  GET_PLAY_UP,
  GET_XBOX_UP,
  GET_NIN_UP,
  GET_MAC_UP,
  GET_LIN_UP,
  GET_AND_UP,
} from "../types";

const initialState = {
  upcomingGames: "",
  newGames: "",
  popularGames: "",
  searchedGames: "",
  favGames: [],
  menu: false,
  current: "",
  currentScreenShots: "",
  currentLogos: "",
  loading: false,
  error: "",

  pcPop: "",
  playPop: "",
  xboxPop: "",
  ninPop: "",
  macPop: "",
  linPop: "",
  andPop: "",

  pcNew: "",
  playNew: "",
  xboxNew: "",
  ninNew: "",
  macNew: "",
  linNew: "",
  andNew: "",

  pcUp: "",
  playUp: "",
  xboxUp: "",
  ninUp: "",
  macUp: "",
  linUp: "",
  andUp: "",
};

const gamesReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_LOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_POPULAR_GAMES:
      return {
        ...state,
        popularGames: action.payload,
        loading: false,
      };
    case GET_UPCOMING_GAMES:
      return {
        ...state,
        upcomingGames: action.payload,
        loading: false,
      };
    case GET_NEW_GAMES:
      return {
        ...state,
        newGames: action.payload,
        loading: false,
      };
    case GET_SEARCHED_GAMES:
      return {
        ...state,
        searchedGames: action.payload,
        loading: false,
      };
    case "GET_CURRENT_GAME":
      return {
        ...state,
        current: action.payload,
        loading: false,
      };
    case "GET_CURRENT_SCREENSHOTS":
      return {
        ...state,
        currentScreenShots: action.payload,
        loading: false,
      };
    case "GET_CURRENT_LOGOS":
      return {
        ...state,
        currentLogos: action.payload,
      };
    case "CLEAR_SEARCHED_GAMES":
      return {
        ...state,
        searchedGames: action.payload,
        loading: false,
      };
    case "CLEAR_CURRENT_GAME":
      return {
        ...state,
        current: "",
      };
    case "ADD_FAV_GAME":
      return {
        ...state,
        favGames: action.payload,
      };
    case "MENU":
      return {
        ...state,
        menu: action.payload,
        loading: false,
      };
    case GAMES_ERROR:
      return {
        ...state,
        error: action.payload,
      };
    default:
      return state;

//1
    case GET_PC_POP:
      return {
        ...state,
        pcPop: action.payload,
        loading: false,
      };

    case GET_PLAY_POP:
      return {
        ...state,
        playPop: action.payload,
        loading: false,
      };

    case GET_XBOX_POP:
      return {
        ...state,
        xboxPop: action.payload,
        loading: false,
      };

    case GET_NIN_POP:
      return {
        ...state,
        ninPop: action.payload,
        loading: false,
      };

    case GET_MAC_POP:
      return {
        ...state,
        macPop: action.payload,
        loading: false,
      };

    case GET_LIN_POP:
      return {
        ...state,
        linPop: action.payload,
        loading: false,
      };

    case GET_AND_POP:
      return {
        ...state,
        andPop: action.payload,
        loading: false,
      };

//2

    case GET_PC_NEW:
      return {
        ...state,
        pcNew: action.payload,
        loading: false,
      };

    case GET_PLAY_NEW:
      return {
        ...state,
        playNew: action.payload,
        loading: false,
      };

    case GET_XBOX_NEW:
      return {
        ...state,
        xboxNew: action.payload,
        loading: false,
      };

    case GET_NIN_NEW:
      return {
        ...state,
        ninNew: action.payload,
        loading: false,
      };

    case GET_MAC_NEW:
      return {
        ...state,
        macNew: action.payload,
        loading: false,
      };

    case GET_LIN_NEW:
      return {
        ...state,
        linNew: action.payload,
        loading: false,
      };

    case GET_AND_NEW:
      return {
        ...state,
        andNew: action.payload,
        loading: false,
      };

//3
    case GET_PC_UP:
      return {
        ...state,
        pcUp: action.payload,
        loading: false,
      };

    case GET_PLAY_UP:
      return {
        ...state,
        playUp: action.payload,
        loading: false,
      };

    case GET_XBOX_UP:
      return {
        ...state,
        xboxUp: action.payload,
        loading: false,
      };

    case GET_NIN_UP:
      return {
        ...state,
        ninUp: action.payload,
        loading: false,
      };

    case GET_MAC_UP:
      return {
        ...state,
        macUp: action.payload,
        loading: false,
      };

    case GET_LIN_UP:
      return {
        ...state,
        linUp: action.payload,
        loading: false,
      };

    case GET_AND_UP:
      return {
        ...state,
        andUp: action.payload,
        loading: false,
      };
  }
};

export default gamesReducer;
