import { Container, Row, Col } from "react-grid-system";
import Navbar from "./components/Navbar";
import Sidebar from "./components/Sidebar";
import MainPage from "./pages/MainPage";
import LibraryPage from "./pages/LibraryPage";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import {
  getPopularGames,
  getUpcomingGames,
  getNewGames,

  getPCPop,
  getPlayPop,
  getXboxPop,
  getNinPop,
  getMacPop,
  getLinPop,
  getAndPop,

  getPCNew,
  getPlayNew,
  getXboxNew,
  getNinNew,
  getMacNew,
  getLinNew,
  getAndNew,

  getPCUp,
  getPlayUp,
  getXboxUp,
  getNinUp,
  getMacUp,
  getLinUp,
  getAndUp,
} from "./redux/actions/gamesAction";

import GlobalStyle from "./components/GlobalStyle";
import {
  HashRouter,
  Route,
  Switch,
} from "react-router-dom";


function App() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getPopularGames());
    dispatch(getUpcomingGames());
    dispatch(getNewGames());

    dispatch(getPCPop());
    dispatch(getPlayPop());
    dispatch(getXboxPop());
    dispatch(getNinPop());
    dispatch(getMacPop());
    dispatch(getLinPop());
    dispatch(getAndPop());

    dispatch(getPCNew());
    dispatch(getPlayNew());
    dispatch(getXboxNew());
    dispatch(getNinNew());
    dispatch(getMacNew());
    dispatch(getLinNew());
    dispatch(getAndNew());

    dispatch(getPCUp());
    dispatch(getPlayUp());
    dispatch(getXboxUp());
    dispatch(getNinUp());
    dispatch(getMacUp());
    dispatch(getLinUp());
    dispatch(getAndUp());

  }, [dispatch]);

  return (
    <>
      <HashRouter>
        <GlobalStyle />
        <Container fluid>
          <Navbar />
          <Row>
            <Col xs="content">
              <Sidebar />
            </Col>
            <Col>               
                  <Switch>
                    <Route exact path="/">
                      <MainPage />
                    </Route>
                    <Route path="/library">
                      <LibraryPage />
                    </Route>
                  </Switch>
            </Col>
          </Row>
        </Container>
      </HashRouter>
    </>
  );
}

export default App;
